'use strict';

define(['swiper', 'Module/echo/1.7.3/echo.min'], function (Swiper, echo) {
    var autoplay = 3000;
    var mySwiper = new Swiper('.swiper-container', {
        autoplay: autoplay, //可选选项，自动滑动
        autoplayDisableOnInteraction: false,
        loop: true, //可选选项，开启循环
        pagination: '.pagination',
        paginationClickable: true,
        speed: 600,
        onSlideChangeEnd: function onSlideChangeEnd(swiper) {
            //循环
            var length = swiper.wrapper.childElementCount;
            if (length === swiper.activeLoopIndex + 1) {
                setTimeout(function () {
                    swiper.swipeTo(0, 500);
                }, autoplay + 200);
            }
            //按需加载
            echo.render();
        },
        onSwiperCreated: function onSwiperCreated() {
            echo.init();
        }
    });
});